
li{
    list-style: none;
}
table{
    display: table;
    -webkit-user-select:none;/* and add the CSS class here instead */ 
-ms-user-select:none; 
-moz-user-select:none; 
     user-select:none;/**禁止选中文字*/
}
td,th{
    display: table-cell;
    padding: 0;
}
.schedule{
    background: rgba(100,255,0,.5);
    width: 0;
    height: 0;
    position: fixed;
    top: 0;
    left: 0;
    pointer-events: none;
}
.calendar-weekTime-cancel{
    background: indianred;
    color: #ffffff;
    padding: 5px;
    border-radius: 5px;
}
.calendar{
    position: relative;
}
.calendar-table{
    border-collapse: collapse;
}
.calendar-table thead{
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
}
.calendar-table tr, .calendar-table td, .calendar-table th{
    border: 1px solid #ddd;
    font-size: 12px;
    text-align: center;
    min-width: 12px;
    line-height: 2.8em;
}
.table{
    box-sizing: border-box;
}
.calendar-head{
    box-sizing: border-box;
}
.calendar-body{
    box-sizing: border-box;
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit;
}
.calendar-time-color{
    background: #f5f5f5;
    border: 1px solid #ddd;
    font-size: 12px;
    text-align: center;
    min-width: 12px;
    line-height: 1.8em;
    transition:background 500ms linear;
}
.selectTime{
    background:rgba(255,100,0,.3);
    transition:background 500ms linear;
}
.calendar-weekTime{
    margin-top: 10px;
   font-size: 12px;
   line-height: 2em;
   max-width: 640px;
}
.calendar-weekTime-cancel{
    float: right
}
.calendar-weekTime ul{
    display: block;
    padding-left: 0;
    padding-right: 90px;
}
.calendar-weekTime ul li{
    display:block;
    text-align: left;
}
.timeList{
    margin: 0 5px;
}
.del{
   color: #333;
}
.del :hover{
    display: block;
    color: red
}



